<template lang='pug'>
section
  h3 {{ title }}
  p(v-if="description !== ''") {{ description }}
  div(:id="id")
    svg.bars
</template>

<script>
import * as d3 from "d3";

const margin = { top: 30, right: 30, bottom: 300, left: 80 };
const width = window.innerWidth * 0.65 - margin.left - margin.right;
const height = window.innerHeight * 0.75 - margin.top - margin.bottom;
const x = d3.scaleBand().range([0, width]).padding(0.2);
const y = d3.scaleLinear().range([height, 0]);

let svg = null;
let xAxis = null;
let yAxis = null;

export default {
  name: "BarPlot",
  props: {
    id: {
      type: String,
    },
    dataset: {
      type: Object,
    },
    title: {
      type: String,
      required: true,
    },
    description: {
      default: "",
      type: String,
    },
  },
  watch: {
    dataset(value) {
      this.update(value);
    },
  },
  mounted() {
    svg = d3
      .select(".bars")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);
    xAxis = svg.append("g").attr("transform", `translate(0, ${height})`);
    yAxis = svg.append("g").attr("class", "myYaxis");

    this.update(this.dataset);
  },
  methods: {
    update(data) {
      svg.selectAll(".bar").remove()
      svg.selectAll(".none").remove()

      if (Object.entries(data).length === 0){
        svg.append('text')
        .attr('class', 'none')
        .attr("x", width / 2)
        .attr("y", height / 2)
        .style('text-align', 'center')
        .style('font-size', '32px')
        .text('無資料')
      }

      x.domain(
        Object.entries(data)
          .sort((a, b) => b[1] - a[1])
          .map((a) => a[0])
      );
      xAxis
        .call(d3.axisBottom(x))
        .selectAll("text")
        .style("text-anchor", "end")
        .attr("dx", "-.8em")
        .attr("dy", ".3em")
        .attr("transform", "rotate(-55)")
        .style("font-size", 16);

      y.domain([0, Math.max(...Object.values(data))]);
      yAxis.transition().duration(1000).call(d3.axisLeft(y))
        .selectAll("text")
        .style("font-size", 16);

      const u = svg.selectAll(".bar").data(Object.entries(data));

      const a = u.enter()
        .append("g")
        .attr('class', 'bar')
        .merge(u)

      a.append('rect')
        .transition()
        .duration(1000)
        .attr("x", (d) => x(d[0]))
        .attr("y", (d) => y(d[1]))
        .attr("width", x.bandwidth())
        .attr("height", (d) => height - y(d[1]))
        .attr("fill", d => /國立成功大學/.test(d[0]) ? "#DB1F48" : "#E5DDC8");

      a.append("text")
        .transition()
        .duration(1000)
        .attr("x", (d) => x(d[0]) + x.bandwidth() / 2)
        .attr("y", (d) => y(d[1]) - 5)
        .attr("text-anchor", "middle")
        .style("fill", "#2c3e50")
        .text((d) => `${d[1]} 人`);
    },
  },
};
</script>

<style scoped>
section {
  width: inherit;
  display: grid;
  justify-items: center;
}

h3 {
  margin: 3rem auto 0;
}

p {
  text-align: start;
}

div {
  width: 100%;
  overflow-x: auto;
  display: grid;
  justify-items: center;
  align-items: center;
}
</style>
